export default {
  watch: {
    owner: {
      deep: true,
      immediate: true,
      handler() {
        this.initData();
      }
    }
  },
  methods: {
      initData() {
        this.form && Object.assign(this.form, JSON.parse(JSON.stringify(this.owner)));
      },
      showRela() {
        this.$emit('showDialog','rela');
      }
  },
  mounted() {
      this.initData();
  }, 
}