import formControl from "./formControl";
// import DsfQITitle from "_platform/pc/question/DsfQITitle";


export default {
  mixins: [formControl],
  props: {
    required: {
      type: Boolean,
      default: true,
    },
    // 是否是问卷组件
    isQuestionControl: {
      type: Boolean,
      default: true
    },
    // 是否需要加入index计算
    hasQuestionIndex:{
      type: Boolean,
      default: true
    },
    // 问卷索引
    questionIndex: {
      type: Number
    },
    // 问卷标题
    questionBody: {
      type: String,
      default: '请输入题干'
    },
    // 关联题逻辑
    logicRela:{
      type: Object,
      default() {
        return {}
      }
    },
    // 跳题逻辑
    logic:{
      type: Object,
      default() {
        return {}
      }
    }
  },
  computed:{
    hasRela:function () {
      return window._.isEmpty(this.logicRela)
    },
    showCtrl:function () {
      if(this.isDesign){
        return true
      }else{
        return this.visible
      }
    }
  },
  components:{ 
    // DsfQITitle 
  }
}